.bottomLinks {
  font-size: 15px;
  display: flex;
  flex-direction: row;
  margin-top: 8px;
}

.notSure {
  flex-grow: 1;
  font-size: 15px;
}

.notSureButton {
  font-size: 15px;
  display: inline;
  font-family: Red Hat Display,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;
  border: none;
  appearance: none;
  background: none;
  color: #e33a11;
  cursor: pointer;
  padding: 0;
  outline: none;
}

.browserHelp {
  color: #e33a11;
  text-decoration: none;
  font-family: Red Hat Display,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;
  font-size: 15px;
}



