.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.7);
  z-index: 10001;
}

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  font-family: Red Hat Display,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;
  z-index: 10002;
  align-items: center;
}

:global .mol-ads-cmp--open {
  :local .wrapper {
    align-items: initial;
    top: 5%;
  }
}

:global .mol-fe-ab-dialog.mol-ads-cmp--open {
  .mol-ads-cmp--banner {
    padding: 15px 20px;
  }
}

:global(body.mol-fe-ab-dialog) {
  overflow-y: hidden;
  max-height: 100vh;
}



