.container {
  width: 680px;
  height: 300px;
  box-sizing: border-box;
  background-color: white;
  background-image: url(./assets/inews.svg);
  background-position: 60px 35px;
  background-repeat: no-repeat;
  padding-top: 30px;
  padding-left: 154px;
  padding-right: 74px;
  border: solid 10px #a1a1a1;
  flex-shrink: 0;
}

.heading {
  font-size: 32px;
  font-weight: 900;
  line-height: 1.15;
  margin-bottom: 10px;
  font-family: Bitter, Georgia, 'serif';
}

.text {
  font-size: 18px;
  line-height: 1.25;
  margin-bottom: 20px;
  font-family: Red Hat Display,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;
}

.viewGuide {
  display: inline;
  border: none;
  appearance: none;
  background: none;
  font-size: 18px;
  color: #e33a11;
  cursor: pointer;
  padding: 0;
  outline: none;
}



